* {
  margin: 0;
  padding: 0; }

html,body {
  width: 100%;
  height: 100%; }

#face-detection-canvas {
  position: absolute; }

#root {
  width: 100%;
  height: 100%; }
#app_container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex; }

.image_closet {
  height: 100%;
  width: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1px;
  background: white;

  input[type="file"] {
    width: 180px;
    margin-top: 10px; }


  img {
    margin-top: 20px;
    width: 200px;
    height: 200px; } }

.canvas_ground {
  flex: 1;
  display: flex;
  background: #f8f8f8;

  .canvas {
    display: flex;
    flex-direction: column;
    width: 400px;
    flex: 1;
    align-items: center;
    padding: 1px;

    div {
      margin-top: 15px;
      width: 500px;
      box-sizing: border-box;
      display: flex;
      align-items: center;

      font-family: "Source Sans Pro";
      font-size: 14px;

      label {
        display: inline-block;
        width: 100px; }

      input[type="range"] {
        flex: 1; } } }

  .image_processing {
    width: 500px; } }

#image_canvas {
  width: 500px;
  height: 500px;
  margin-top: 50px;
  background: white; }



// image processing menu

.image_processing {
  font-family: "Source Sans Pro";
  background: white;

  h1 {
    size: 16px;
    margin-top: 20px;
    margin-left: 20px; } }

.action_menu {
  margin-left: 20px;
  div {
    display: flex;
    align-items: center;
    font-family: "Source Sans Pro";
    margin-top: 10px;

    input {
      margin-left: 10px; }

    label {
      margin-left: 5px;
      font-size: 14px; } } }

